* {
  margin: 0;
  padding: 0;
}

body {
  background: #ffffff;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #324d52;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
}

.header {
  width: 540px;
  height: 50px;
  margin: 0 auto;
  border-bottom: 1px solid #e1e1e1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media (max-width: 320px) {
  .header {
    width: 100vw;
  }
}

.logo,
.menu {
  line-height: 48px;
  padding: 0 20px;
}

.container {
  width: 320px;
  margin: 50px auto;
  height: 100px;
  text-align: center;
}

.timer {
  font-size: 2em;
  padding: 20px;
}

button {
  cursor: pointer;
}

.actions .btn {
  margin: 5px;
}

.btn {
  border: 1px solid #e1e1e1;
  border-radius: 3px;
  padding: 4px 12px;
  color: #888;
  font-size: 18px;
  font-weight: 600;
  background: #ffffff;
}

.btn-primary {
  background: #673ab7;
  color: #f1f1f1;
}

.btn-secondary {
  background: #FF5E99;
  color: #ffffff;
}

.btn-red {
  background: #FF3333;
  color: #ffffff;
  text-align: center;
}

.btn-start {
  background: #9FDE23;
  color: #f1f1f1;
  text-align: center;
}

.btn-round {
  width: 70px;
  height: 70px;
  border: 0;
  padding: 0;
  font-weight: 600;
  border-radius: 35px;
}

.btn-animate {
  -webkit-animation: heart-beat-green infinite 1s linear;
          animation: heart-beat-green infinite 1s linear;
}

@-webkit-keyframes heart-beat {
  0% { 
     -webkit-box-shadow: 0px 0px 0px red; 
             box-shadow: 0px 0px 0px red; 
    /* transform: scale(1.0); */
  }
  60% { 
     -webkit-box-shadow: 0px 0px 10px red; 
             box-shadow: 0px 0px 10px red; 
    /* transform: scale(0.8); */
  }
  80% { 
     -webkit-box-shadow: 0px 0px 0px red; 
             box-shadow: 0px 0px 0px red; 
    /* transform: scale(0.9); */
  }
  90% { 
     -webkit-box-shadow: 0px 0px 6px red; 
             box-shadow: 0px 0px 6px red; 
    /* transform: scale(0.8); */
  }
}

@keyframes heart-beat {
  0% { 
     -webkit-box-shadow: 0px 0px 0px red; 
             box-shadow: 0px 0px 0px red; 
    /* transform: scale(1.0); */
